.event {
  color: $c-black;

  &__data-container {
    @include column-gap;

    display: grid;
    align-self: center;
    grid-template-columns: repeat(7, 1fr);

    @include onTablets {
      display: flex;
      gap: 24px;
    }
  }

  &__date-info {
    grid-column: 1 / 3;

    @include onMobile {
      display: none;
    }
  }

  &__copy {
    grid-column: 3 / 8;
  }

  &__btn-container {
    @include column-gap;

    display: grid;
    grid-template-columns: repeat(5, 1fr);

    @include onMobile {
      display: block;
    }
  }

  &__btn {
    grid-column: 1 / 4;

    @include onTablets {
      grid-column: 1 / 3;
    }
  }

  &__date {
    @include style-2;

    margin-bottom: 10px;

    color: $c-black;
  }

  &__time {
    @include style-3;

    text-align: right;
    color: $c-orange;
  }

  &__type {
    @include overline;

    margin-bottom: 10px;
  }

  &__title {
    @include h2;

    margin-bottom: 8px;
  }

  &__description {
    @include main-text;

    margin-bottom: 60px;

    @include onTablets {
      margin-bottom: 30px;
    }

    @include onMobile {
      margin-bottom: 40px;
    }
  }

  &__img {
    display: block;
    width: 100%;

    @include onTablets {
      display: none;
    }
  }
}
